const gallery = document.querySelector('.gallery');
const createHTML = (images) => {

  const galleryImages = images.reduce((htmlString, {preview, original, description}) => {
    return `${htmlString} <li class="gallery-item">
                  <a class="gallery-link" href="${original}">
                    <img
                      class="gallery-image"
                      src="${preview}"
                      alt="${description}"
                    />
                  </a>
                </li>`
  }, '')

  gallery.insertAdjacentHTML('beforeend', galleryImages);
}

createHTML(images);

document.addEventListener('DOMContentLoaded',  () => {
  const link = document.querySelectorAll('.gallery-link');
  Array
    .from(link)
    .forEach(element => element.addEventListener('click', (e) => {
      e.preventDefault();
    }))

  new SimpleLightbox(link, {
    className: 'gallery-link',
    closeText: ['<i class="fa fa-times">&#10006;</i>'],
    navText: ['<i class="fa fa-angle-left">&lt;</i>','<i class="fa fa-angle-right">&gt;</i>'],
    captionsData: 'alt',
    captionDelay: 250
  })
});